import React from 'react';
import { Box, Typography } from '@mui/material';
import Project from "./Project";
import PacmanImg from "../assets/pacman.gif.gif";
import XiImg from "../assets/xi.png";
import CovidDataImg from "../assets/covid.gif";

export default function Projects(props){
  return (
    <Box>
    <Typography sx={{width: "65%", mb: "40px"}}>
      I always love learning new technologies, and the best way of learning
      is by doing! My projects display a wide range of skills from 
      <span>functional programming</span>
      to 
      <span>web development</span>.
    </Typography>
    <Project 
        title="Xi Compiler"
        tech={["OCaml", "x86 Assembly"]}
        imageSrc={XiImg}
      >
       <Typography>
        As part of my compilers course (the most challenging CS course at Cornell!), 
        I worked in a 
        <span>team</span>
        to build a 
        <span>fully functional compiler</span>
        to 
        <span>x86 machine code</span>
        from scratch (code available upon request).
       </Typography>
      </Project>
      
      <Project 
        title="Covid-19 Data Visualization"
        tech={["React.js", "Flask", "SQL"]}
        imageSrc={CovidDataImg}
        link="https://github.com/amandreasen/covid_data_vis"
      >
        <Typography>
        In the midst of pandemic-induced boredom, I taught myself 
        <span>React</span> and<span>SQL</span>
        to implement my<span>first independent project</span> -- a website that will chart 
        and compare Covid-19 case data from user location queries.
       </Typography>
      </Project>

      <Project 
        title="OCaml Pacman"
        tech={["OCaml"]}
        imageSrc={PacmanImg}
        link="https://github.coecis.cornell.edu/apr65/cs3110-final-project"
      >
      <Typography>
        In one of my first open-ended software projects, I led a 
        <span>team</span>
        to design and implement a GUI Pacman clone complete with
        <span>animation</span>
        and a<span>game AI</span>.
       </Typography>
      </Project>
    </Box>
  )
}