import React from 'react';
import {Chip, Box, Stack, Container, Grid, Typography, Link, List, ListItem} from "@mui/material";
import GithubIcon from "../assets/github.png";
import Icon from "./Icon"
import "./Project.css"

export default function Project({title, imageSrc, link, tech, children}){
  return (
    <Box sx={{width: "1000px"}}>
    <Grid container spacing={3} 
    direction="column"
    alignItems="center"
    justifyContent="center"
    sx={{mb:"80px", width: "100%"}}
    >
      <Grid item xs={12}>
        <Typography variant="h4" display="inline"> {title} </Typography>
        {/* <Typography display="inline"> --- </Typography> */}
        {/* <Typography variant="h5" display="inline">  */}
        <Typography id="tech-label" display = "inline">
        {tech.map((tech) => 
          <Chip label={tech} variant="outlined" color="primary"
          sx={{mx: "10px"}}
          />
        )} 
        </Typography>
        {/* </Typography> */}
      </Grid>
      <Grid item xs={12}>
        <Box id="project-info">
          <Stack>
            <img src={imageSrc} alt="oops" width="200px" height="200px"/>
            {link && 
            <Box>
              <Icon src={GithubIcon} link={link}/>
            </Box>}
          </Stack>
          <Container sx={{width: "500px"}}>
            {children}
          </Container>
        </Box>
      </Grid>
    </Grid>
    </Box>
  )
}