import './App.css';
import "@fontsource/montserrat";
import "@fontsource/open-sans";
import {AppBar, Button, Container, Typography, Box, StyledEngineProvider} from '@mui/material';
import NavBarItem from './components/NavBarItem';
import Intro from './components/Intro';
import Experiences from './components/Experiences';
import Projects from './components/Projects';
import FileDownloadIcon from '@mui/icons-material/FileDownload';

function App() {
  const onDownload = () => {
    const link = document.createElement("a");
    link.download = `AmandaAndreasenResume.pdf`;
    link.href = "resume.pdf";
    link.click();
  };

  return (
    <StyledEngineProvider injectFirst>
      <AppBar position="sticky" sx={{padding: "10px"}}>
        <Container sx={{display: "flex", flexDirection: "row"}}>
        <NavBarItem 
            text="About Me" 
            link="#nav-bar-0"
          />
          <NavBarItem 
            text="Where I've Worked" 
            link="#nav-bar-1"
          />
          <NavBarItem
            text="Projects I've Done"
            link="#nav-bar-2"
          />
          <Button 
          onClick={onDownload}
          variant="contained" 
          endIcon={<FileDownloadIcon/>}
          sx={{width: "400px", backgroundColor : "white"}}
          >
            <Typography id="download-text">
              Download Resume
            </Typography>
          </Button>
        </Container>
      </AppBar>
      <Box>
        <Box id="nav-bar-0">
          <Intro/>
        </Box>
        
        <Box id="nav-bar-1">
          <Typography variant="h2">1. Where I've Worked</Typography>
          <Experiences/>
        </Box>
        
        <Box id="nav-bar-2">
          <Typography variant="h2" sx={{mb: "15px"}}>2. Projects I've Done</Typography>
          <Projects/>
        </Box>
        
      </Box>
   
    </StyledEngineProvider>
  );
}

export default App;
