import React, {useState} from 'react';
import { Box, Typography, Stack} from '@mui/material';
import {Timeline, TimelineConnector, TimelineItem, TimelineSeparator, TimelineDot, TimelineContent} from "@mui/lab";
import JS from "../assets/js.png";
import OCaml from "../assets/ocaml.png";
import Wayfair from "../assets/wayfair.jpg";
import ReactIcon from "../assets/react.jpg";
import Redux from "../assets/Redux.png";
import "./Experiences.css";

export default function Experiences(props){
  const [selectedExperience, setSelectedExperience] = useState("js")

  const isJSSelected = selectedExperience === "js"
  const isWayfairSelected = selectedExperience === "wayfair"

  return (
    <Box>
      <Typography sx={{width: "60%", mb: "20px"}}>
      I have almost one year of experience building
      <span>
      real software
      </span> 
      that is used by 
      successful companies. Please click to toggle between my experiences if you 
      would like to learn more!
      </Typography>
      <Box id="timeline" sx={{width: "200px", mx: "-250px"}}>
        <Timeline position="left" sx={{width: "100%"}}>
          <TimelineItem onClick={() => setSelectedExperience("js")}>
            <TimelineSeparator>
              <TimelineDot color={isJSSelected ? "success" : "grey"}/>
              <TimelineConnector sx={{height: "200px"}}/>
            </TimelineSeparator>
            <TimelineContent>
              <img src={JS} alt="jane street" width="50" height="50"/>
              <Typography id="timeline-label" width="50">
                2022: Jane Street
              </Typography>
              
            </TimelineContent>
          </TimelineItem>

          <TimelineItem onClick={() => setSelectedExperience("wayfair")}>
            <TimelineSeparator>
              <TimelineDot sx={{mr: "20px"}}color={isWayfairSelected ? "success" : "grey"}/>
            </TimelineSeparator>
            <TimelineContent>
              <img src={Wayfair} alt="jane street" width="50" height="50"/>
              <Typography id="timeline-label">
                2021: Wayfair
              </Typography>
            </TimelineContent>
          </TimelineItem>
        </Timeline>

        <Box sx={{width: "2500px"}}>
          <Typography>
            { isJSSelected 
            ? 
              <Box>
                <Typography sx={{mb: "50px"}}>
                Over the course of one summer, I successfully delivered 
                <span>two complete projects</span> 
                across
                <span>two distinct software</span>
                engineering teams -- in doing so, I worked with software trading
                infrastructure that supports up to<span>$13 billion</span>in
                trades per day.
                </Typography>
                
                <Typography id="bold">
                Technology:
                </Typography>
                
                <img src={OCaml} width="100px" height="100px" alt="ocaml logo"/>
              </Box>
              
            :

              <Box>
                <Typography sx={{mb: "50px"}}>
                Over the course of six months, I fully integrated into a
                <span>frontend team</span>
                team to work on products that generate more than 
                <span>$500 million</span> in profits per year.
                </Typography>
                
                <Typography id="bold">
                Technology:
                </Typography>
                
                <Stack direction="row">
                  <img src={ReactIcon} width="100px" height="100px" alt="ocaml logo"/>
                  <img src={Redux} width="100px" height="100px" alt="ocaml logo"/>
                </Stack>
                
              </Box>
            }
          </Typography>
        </Box>
      </Box>
    </Box>);
}